section .contactcontainer{
    position: relative;
    min-width: 1100px;
    min-height: 550px;
    display: flex;
    z-index: 1000;

    @media all and (max-width: 1280px) { 
        min-width: 1000px;
        min-height: 500px;
    }

    @media all and (max-width: 1024px){
        min-width: 800px;
        min-height: 500px;
     }

     @media all and (max-width: 768px) {
        min-width: 650px;
        min-height: 700px;
      }

     @media all and (max-width: 480px) {
        min-width: 400px;
        min-height: 670px;
        justify-content: center;
        // flex-direction: column;
      }

    .contactinfo{
        position: absolute;
        top: 40px;
        width: 350px;
        height: calc(100% - 80px);
        // background: rgb(85, 2, 118);
        background: gray;
        z-index: 1;
        padding: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2);
        border-radius: 10px;

        @media all and (max-width: 480px) {
            justify-self: center;
            top: 0;
            width: 80%;
            height: 250px;
          }

        @media all and (max-width: 768px) {
            height: 300px;
          }


        h2{
            color: rgb(255, 255, 255);
            font-size: 24px;
            font-weight: 500;
        
        }


        .info{
            position: relative;
            margin: 20px 0;
            padding: 0;


            li{
                color: white;
                position: relative;
                list-style: none;
                display: flex;
                margin: 10px 0;
                cursor: pointer;
                align-items: flex-start;


                span:nth-child(1){
                    width: 30px;
                    min-width: 30px;
                }


                span:nth-child(1) img{
                    // filter: invert(1);
                    max-width: 150%;
                }


                span:nth-child(2){
                    padding-left: 30px;
                    width: 30px;
                    min-width: 30px;
                }


                &:hover span:nth-child(1) img
                {
                    filter: invert(1);
                    opacity: 1;
                }
            }
        }
    }
}

