section .contactcontainer .contactinfo .sci{
    position: relative;
    display: flex;
}

section .contactcontainer .contactinfo .sci li{
    list-style: none;
    margin-right: 15px;
}

section .contactcontainer .contactinfo .sci li a{
    text-decoration: none;
}

section .contactcontainer .contactinfo .sci li a img{
    max-width: 80%;
}

section .contactcontainer .contactinfo .sci li:hover a img{
    filter: invert(1);
}

section .contactcontainer .contactForm{
    position: absolute;
    padding: 70px 50px;
    background: white;
    margin-left: 150px;
    padding-left: 250px;
    width: calc(100% - 150px);
    height: 100%;
    box-shadow: 0 50px 50px rgba(0, 0, 0, 0.5);
    border-radius: 20px;

    @media all and (max-width: 480px) {
        position: absolute;
        top: 170px;
        margin-left: 0;
        padding-left: 50px;
        padding-top: 140px;
        width: 90%;
        height: 550px;
      }


      h2{
        color: black;
        font-size: 24px;
        font-weight: 500;
    }


    .formBox{
        position: relative;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        padding-top: 30px;


        .inputBox{
            position: relative;
            margin: 0 0 35px 0;


            input,textarea{
                width: 100%;
                padding: 5px 0;
            
                font-size: 18px;
                font-weight: 300;
                color: #333;
                border: none;
                border-bottom: 1px solid #777;
                outline: none;
            }


            span{
                position: absolute;
                left: 0;
                padding: 5px 0;
                font-size: 18px;
                font-weight: 300;
                color: #333;
                transition: 0.3s;
                pointer-events: none;
            }
        }


        .w50{
            width: 47%;

            // @media all and (max-width: 768px) {
            //     width: 100%;
            //   }
        }


        .w100{
            width: 100%;
        }
    }
}

