.maincontainer{
    height: 100vh;
    display: flex;
    background-color: black;


    @media all and (max-width: 1024px){
        flex-direction: column;
     }
}

.setupcontainer{
    flex-basis: 50%;


    @media all and (max-width: 1024px){
        flex-basis: 60%;
     }
}

.setupimg{
    margin-top:5% ;
    height: 666px;
    width: 1000px;
}


.textcontainer{
    flex-basis: 35%;
    margin-top: 15%;
    margin-left: 10%;


    @media all and (max-width: 1024px){
        flex-basis: 15%;
        margin-top: 10%;
        margin-left: 10%;
     }
}

#greet{
    position: relative;
    color: white;
    font-size: 70px;


    @media all and (max-width: 480px){
        font-size: 50px;
     }
    @media all and (max-width: 768px){
        font-size: 60px;
     }
}

.container{
    height: 50vh;
  }
  
  .container{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;


    img{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: contain;
      }
  }
  

  .static-txt{
    color: #fff;
    font-size: 60px;
    font-weight: 400;
  }


  .dynamic-txts{
    // margin-left: 15px;
    height: 90px;
    line-height: 90px;
    overflow: hidden;
    padding-left: 0;
  }


  .dynamic-txts li{
    list-style: none;
    color: rgb(132, 194, 255);
    font-size: 68px;
    font-weight: 500;
    position: relative;
    top: 0;
    animation: slide 12s steps(4) infinite;


    @media all and (max-width: 480px){
        font-size: 70px;
     }
  }


  @keyframes slide {
    100%{
      top: -360px;
    }
  }
  .dynamic-txts li span{
    position: relative;
    // margin: 5px 0;
    // line-height: 86px;
  }
  .dynamic-txts li span::after{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    height: 101%;
    width: 100%;
    background: #000000;
    border-left: 2px solid rgb(132, 194, 255);
    animation: typing 3s steps(10) infinite;
  }
  @keyframes typing {
    40%, 60%{
      left: calc(100% + 30px);
    }
    100%{
      left: 0;
    }
  }